/* SideBar.css */

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 18%;
  padding-top: 20px;
  background-color: grey; /* Change to whatever color you prefer */
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
 
}

.profile-img {
  width: 8rem;
  height: 10rem;
  border-radius: 50%;
  margin-bottom: 2rem;
}
