body, html {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
}
.main-bg {
  width: 100%;
  height: 100vh; 
  object-fit: cover;
  position: relative;
  filter: brightness(50%);
}
.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: left;
  color: white;
}
.section {
  margin-left: 22%;
  text-align: left;
}
.figure-wrapper {
  padding-top: 2rem;
}

.figure-wrapper h2 {
  margin-bottom: 2rem;
}

.figure-wrapper h3 {
  margin-top : 2rem;
  margin-bottom: 2rem;
  font-weight: bold;
}

.figure-wrapper li {
  margin-bottom: 1rem;
}

.figure-content {

  display: flex;
}

.figure-info {
  flex: 1;
  margin-left: 5rem;
  text-align: left;
}
.resume-section ul {
  list-style-type: disc;
}

#footer {
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem 0;
}

#footer a {
  color: white;
  margin: 0 1rem;
}

#footer p {
  margin-top: 0.5rem;
}


@media (max-width: 768px) {
  .figure, .resume-section {
    margin-left: 5%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
